
@import "../../assets/styles/base/_variables";
.z-header{
    position: fixed;
    top: 0;
    width: 100%;
    left: 0;
    right: 0;
    z-index: 1000;
    .van-nav-bar{
        height: 0.88rem;
        .van-nav-bar__left{}
        .van-nav-bar__title{
            font-size: 0.36rem;
            color: #4A4A4A;
            letter-spacing: 0;
            text-align: center;
            max-width: 3.5rem;
            font-weight: initial;
            line-height: 0.88rem;
        }
        .van-nav-bar__right{
            bottom: 0.15rem;
        }
    }
    .left-side{
        height: 0.88rem;
        line-height: initial;
        display: flex;
        align-items: center;
        vertical-align: baseline;
        .icon-jiantou{
            transform: rotate(-180deg);
            display: inline-block;
            line-height: initial;
            &::before{
                font-size: 0.28rem;
                color: #008CD7;
            }
        }
        span{
            font-size: 0.3rem;
            color: #008CD7;
            letter-spacing: 0;
            position: absolute;
            width: max-content;
            left: 0.3rem;
        }
    }
    .right-side{
        border: 1px solid #DADADA;
        border-radius: 1rem;
        width: 1.6rem;
        height: 0.59rem;
        // line-height: 0.59rem;
        display: flex;
        align-items: center;
        box-sizing: border-box;
        .otherwise{
            flex: 1;
            height: 0.59rem;
            line-height: 0.59rem;
            font-size: 0.32rem;
            .@{prefix}-icon{
                font-size: 0.32rem;
                vertical-align: middle;
                line-height: 0.59rem;
            }
        }
        .split{
            vertical-align: middle;
            line-height: 0.59rem;
            font-size: 0.32rem;
        }
        .exit{
            flex: 1;
            height: 0.59rem;
            line-height: 0.59rem;
            font-size: 0.32rem;
            .@{prefix}-icon{
                font-size: 0.32rem;
                vertical-align: middle;
                line-height: 0.59rem;
            }
        }
    }
}

