.z-header {
  position: fixed;
  top: 0;
  width: 100%;
  left: 0;
  right: 0;
  z-index: 1000;
}
.z-header .van-nav-bar {
  height: 0.88rem;
}
.z-header .van-nav-bar .van-nav-bar__title {
  font-size: 0.36rem;
  color: #4A4A4A;
  letter-spacing: 0;
  text-align: center;
  max-width: 3.5rem;
  font-weight: initial;
  line-height: 0.88rem;
}
.z-header .van-nav-bar .van-nav-bar__right {
  bottom: 0.15rem;
}
.z-header .left-side {
  height: 0.88rem;
  line-height: initial;
  display: flex;
  align-items: center;
  vertical-align: baseline;
}
.z-header .left-side .icon-jiantou {
  transform: rotate(-180deg);
  display: inline-block;
  line-height: initial;
}
.z-header .left-side .icon-jiantou::before {
  font-size: 0.28rem;
  color: #008CD7;
}
.z-header .left-side span {
  font-size: 0.3rem;
  color: #008CD7;
  letter-spacing: 0;
  position: absolute;
  width: max-content;
  left: 0.3rem;
}
.z-header .right-side {
  border: 1px solid #DADADA;
  border-radius: 1rem;
  width: 1.6rem;
  height: 0.59rem;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}
.z-header .right-side .otherwise {
  flex: 1;
  height: 0.59rem;
  line-height: 0.59rem;
  font-size: 0.32rem;
}
.z-header .right-side .otherwise .tianxinfinancialchannels-icon {
  font-size: 0.32rem;
  vertical-align: middle;
  line-height: 0.59rem;
}
.z-header .right-side .split {
  vertical-align: middle;
  line-height: 0.59rem;
  font-size: 0.32rem;
}
.z-header .right-side .exit {
  flex: 1;
  height: 0.59rem;
  line-height: 0.59rem;
  font-size: 0.32rem;
}
.z-header .right-side .exit .tianxinfinancialchannels-icon {
  font-size: 0.32rem;
  vertical-align: middle;
  line-height: 0.59rem;
}
/* 兼容iphone6，iphone7，iphone8s */
@media (device-height: 667px) and (-webkit-min-device-pixel-ratio: 2) {
  .z-header .left-side span {
    top: 0.2rem;
  }
}



