
@import "../../assets/styles/base/_variables";
.z-tabbar{
  .van-tabbar {
    height: 1.08rem !important;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #ffffff;
    box-shadow: 0 0 .1rem 0 #EEEEEE;
    &.van-hairline--top-bottom::after{
      border-top: 0;
    }
  }
  .van-tabbar-item {
    &.van-tabbar-item--active{
      .@{prefix}-icon {
        font-size: 0.36rem;
        color: #008CD7;
      }
      .van-tabbar-item__text span {
        font-size: 0.2rem;
        color: #008CD7;
      }
    }
    .van-tabbar-item__icon {
      margin-bottom: 0.11rem;
    }
    .@{prefix}-icon {
      font-size: 0.36rem;
      color: #9B9B9B;
      &.icon-xinzengdianzhan{
        font-size: 0.48rem;
        color: #fff;
        line-height: 1rem;
        // background: linear-gradient(0deg, #008CD7, #00C0ED);
        // -webkit-background-clip: text;
        // border-radius: 50%;
        // box-shadow: 0 0 10px 0 rgba(0,140,215,0.40);
      }
    }
    .van-tabbar-item__text span {
      font-size: 0.2rem;
      color: #9B9B9B;
    }
    &.scan-btn{
      .van-tabbar-item__text{
        margin-top: -0.7rem;
        .icon-area{
          width: 1rem;
          height: 1rem;
          border-radius: 50%;
          background: linear-gradient(0deg, #008CD7, #00C0ED);
          box-shadow: 0 0 10px 0 rgba(0,140,215,0.40);
          text-align: center;
        }
      }
    }
  }
  .van-info{
      width: 1.2em;
      height: 1.2em;
      overflow: hidden;
      color: #f44;
  }
}



